import React,{useEffect, useState} from "react";
import "../index.css";
import "../styles/invitestyle.css";
import Selector from "./invite/Selector";
import FoodOption from "./invite/FoodOption";
import Field from "./invite/Field";
import {addData} from './AddGetData';


import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";

export default function()
{
    const initialInvite = {
        name: "2",
        date: "",
        email: "",
        phone: "",
        address: "",
        amount: 0,
        foods: [],
        deserts: []
    }

    const [invite, setInvite] = useState(initialInvite)

    const [iserror, setIserror] = useState(true);
    const [price, setPrice] = useState(0);
    const [status, setStatus] = useState("");

    useEffect(function inviteChanged() {
        if (!iserror)
        console.log(invite);
        setIserror(true);
        calcPrice();
    }, [invite]);

    function handleChange(val, isAdding=true) {
        setIserror(false);
        const {name, value} = val;
        if (isAdding){
            setInvite((prev) => {
                if (Array.isArray(invite[name])){
                        const tempArr = invite[name];
                        tempArr.push(value);
                        return{
                            ...prev,
                            [name]: tempArr
                        }}
                else{
                    return{
                        ...prev,
                        [name]:value
                    }
                }
            })
        }else if(Array.isArray(invite[name])){
            var tempArr = invite[name];
            tempArr = removeItemAll(tempArr, value);
            setInvite((prev) => {
            return{
                ...prev,
                [name]: tempArr
            }})
        }

        function removeItemAll(arr, value) {
            var i = 0;
            while (i < arr.length) {
              if (arr[i] === value) {
                arr.splice(i, 1);
              } else {
                ++i;
              }
            }
            return arr;
          }
    }

    function calcPrice(){
        var price = invite.amount * 20 * (invite.foods.length > 0 ? invite.foods.length*1.5 : 1) * (invite.deserts.length > 0 ? invite.deserts.length*1.5 : 1);
        
        setPrice(price);
    }

    const addgetData= async()=>{
        await addData({data: invite, price: price});
        setStatus(" ");
      }

    return( 
        <div className="invite-page">
            {/* ---------- Back to main page ----------- */}
            <div class="form-group row">
                <div class="col-sm-12">
                <Link to="/"><button type="button" class="btn btn-warning">  </button></Link>
                </div>
            </div>
            <h1>{status}</h1>


            <div className="invite-part">
                {/* ---------- Name ----------- */}
                <Field label=" " type="text" name="name" func={handleChange} />
                <Field label="" type="date" name="date" func={handleChange} />
                <Field label="" type="tel" name="phone" func={handleChange} />
                <Field label="" type="text" name="address" func={handleChange} />
                <Field label="" type="text" name="city" func={handleChange} />
                <Field label="" type="email" name="email" func={handleChange} />
                
                {/* ---------- Participates ----------- */}
                <div class="form-group row">
                    <div class="col-sm-10 col-8">
                        <div class="col-auto my-1">
                            <Selector func={handleChange} />
                            
                        </div>
                    </div>
                    <label class="col-sm-2 col-4 col-form-label"></label>
                    </div>
                {/* ---------- main food ----------- */}
                
                <div class="form-group row choose-area">
                    
                    <div class="col-10">
                    <div class="form-group row">
                        <FoodOption name="foods" food="  " func={handleChange}/>
                        <FoodOption name="foods" food=" " func={handleChange}/>
                        <FoodOption name="foods" food="  " func={handleChange}/>
                        </div>
                    </div>
                    <label class="col-2 col-form-label"> </label>
                    
                </div>

                {/* ---------- main food ----------- */}
                
                <div class="form-group row choose-area">
                    
                    <div class="col-10">
                    <div class="form-group row">
                    <FoodOption name="deserts" food="  " func={handleChange} />
                        <FoodOption name="deserts" food="" func={handleChange} />
                        <FoodOption name="deserts" food="  " func={handleChange} />
                        </div>
                    </div>
                    <label class="col-2 col-form-label"></label>
                    
                </div>

                {/* ---------- Evaluated price ----------- */}
                <div class="form-group row">
                    <div class="col-sm-12">
                    <h1 className="eval-price-text"> : {price}</h1>
                    </div>
                </div>

                {/* ---------- Send ----------- */}
                <div class="form-group row">
                    <div class="col-sm-12">
                    
                    <form onSubmit={addgetData}>
                    <button type="submit" class="btn btn-success" name="datajson" ></button>
                    </form>
                    </div>
                </div>
                
            </div>
        </div>
    )
}
